<script>
export default {
    data() {
        return {
            items: [],
        };
    },

    methods: {
        productDetail(productPk) {
            this.$router.push({
                name: "Detail",
                query: { productPk: productPk },
            });
        },
    },
    mounted() {
        let obj = this;

        obj.$axios
            .get("http://localhost:9000/productMain")
            .then(function (res) {
                console.log("axios   ");
                obj.items = res.data;
            })
            .catch(function (err) {
                console.log("axios   ");
                console.log(err);
            });
    },
};
</script>

